import {
	uniIcon
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniIcon
	},
	data() {
		return {
			addrLe: true,
			timeLe: true,
			num:2
		}
	},
	onLoad() {
		uni.setNavigationBarTitle({
		　　title:'购物车'
		})
	},
	methods: {
		//增加规格数量
		addNum:function(){
			this.num=parseInt(this.num)+1
		},
		//减少规格数量
		minusNum:function(){
			if(this.num<1){
				this.num = 0
			}else{
				this.num=parseInt(this.num)-1
			}
		},
		goAddress:function(){
			uni.showToast({
				icon:"loading",
				title:"loading..."
			})
			uni.navigateTo({
				url:"/pages/address/address"
			})
		}
	}
}
